.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #fff;
  /* Add more styles as needed */
}

.sidebar {
  position: fixed;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  /* Add more styles as needed */
}

.main-content {
  margin-top: 80px; /* Adjust based on navbar height */
  margin-left: 60px; /* Adjust based on sidebar width */
  /* Add more styles as needed */
}

/*portfolio*/

.portfolio-page {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  height: 100vh;
  background-color: #f7f7f7; /* Match this with your design's background color */
}

.intro {
  flex: 1;
}

.intro h1 {
  font-size: 3rem;
  font-weight: bold;
}

.intro h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.intro p {
  font-size: 1.2rem;
}

.hero-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.hero-image img {
  max-width: 100%;
  height: auto;
}

